.login-mobile {
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  background: #fff;
}

.login-mobile-welcome {
  padding: 200px 0 60px 0;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login-mobile-welcome > img {
  width: 100px;
  height: 100px;
}

.login-mobile-welcome > span {
  margin-top: 30px;
  font-size: 30px;
  color: #333;
}

.login-mobile-content {
  padding: 0 30px;
  width: 100%;
  box-sizing: border-box;
}

.login-mobile-register-forget {
  padding: 0 60px;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.login-mobile-register-forget > span {
  color: #1976d2;
}
